.areaForLoader {
  background: linear-gradient(90deg, #0c0c0c, #bdbdbd);
  overflow: hidden;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 9999; }

.loader {
  height: 40px;
  width: 40px;
  position: absolute;
  left: 50%;
  margin-left: -20px;
  top: 50%;
  margin-top: -20px; }
  .loader:before, .loader:after {
    content: "";
    height: 40px;
    width: 40px;
    border: 8px solid rgba(255, 255, 255, 0.5);
    border-radius: 10px;
    position: absolute;
    top: 0; }
  .loader:before {
    animation: animate 2s infinite linear; }
  .loader:after {
    animation: animate2 2s infinite linear; }

@keyframes animate {
  100% {
    transform: rotate(180deg) skew(360deg); } }

@keyframes animate2 {
  100% {
    transform: rotate(-180deg) skew(-360deg); } }

